/* Main SCSS File */
.vertical-center {
  display: flex;
  align-items: center;
  justify-content: center; }

h1 {
  color: blue; }

.navbar {
  height: 86px; }

.navbar-default {
  background-color: #fff;
  border-bottom: 1px solid #e6e6e6; }

#hire-button {
  float: right; }

ul.navbar {
  display: inline; }

@media (max-width: 768px) {
  #hire-button {
    display: none; } }
